import CartItemCard from 'components/CartItemCard/CartItemCard';
import Checkbox from 'components/Checkbox/Checkbox';
import Spinner from 'components/Spinner/Spinner';
import DivideLine from 'components/DivideLine/DivideLine';
import Button from 'components/Button/Button';
import useCart from 'pages/CartPage/useCart';
import * as S from 'pages/CartPage/CartPage.styled';

function CartPage() {
  const {
    isLoading,
    error,
    cart,
    checkedFlags,
    totalPrice,
    handleChangeQuantity,
    handleCheck,
    handleCheckAll,
    removeCartItem,
    removeAllCartItem,
  } = useCart();

  if (error) {
    alert(error);
  }

  if (isLoading) {
    return <Spinner />;
  }

  return (
    <S.PageBox>
      <S.Title></S.Title>
      <DivideLine />
      <S.SectionsBox>
        <S.LeftSection>
          <S.ControlBox>
            <S.SelectAllBox>
              <Checkbox
                id="select-all-checkbox"
                name="select-all-checkbox"
                checked={Object.values(checkedFlags).every(
                  (checked) => checked
                )}
                onChange={handleCheckAll}
              />
              <label htmlFor="select-all-checkbox"></label>
            </S.SelectAllBox>
            <S.RemoveSelectedButtonBox onClick={removeAllCartItem}>
              
            </S.RemoveSelectedButtonBox>
          </S.ControlBox>
          <S.Subtitle>  ({cart.length})</S.Subtitle>
          <DivideLine color="gray" />
          <ul>
            {cart.map((cart) => {
              const { cartItemId } = cart;

              return (
                <li key={cartItemId}>
                  <CartItemCard
                    cart={cart}
                    checked={checkedFlags[cartItemId] ?? true}
                    onChangeQuantity={handleChangeQuantity(cart)}
                    onCheck={handleCheck(cart)}
                    onClickRemove={removeCartItem(cart)}
                  />
                  <DivideLine thickness="thin" />
                </li>
              );
            })}
          </ul>
        </S.LeftSection>
        <S.RightSection>
          <S.RightSectionTopBox>
            <S.Subtitle></S.Subtitle>
          </S.RightSectionTopBox>
          <DivideLine thickness="thin" />
          <S.TotalPriceBox>
            <S.HighlightedText></S.HighlightedText>
            <S.HighlightedText>
              {totalPrice.toLocaleString('ko-KR')}
            </S.HighlightedText>
          </S.TotalPriceBox>
          <S.OrderButtonBox>
            <Button>
               (
              {Object.values(checkedFlags).filter((checked) => checked).length}
              )
            </Button>
          </S.OrderButtonBox>
        </S.RightSection>
      </S.SectionsBox>
    </S.PageBox>
  );
}

export default CartPage;
